import React, { Component } from "react";

import Main from "./views/main";

class App extends Component {
  constructor() {
    super();

    this.state = {
      tab: 0,
    };
  }

  handleTabChange = (e) => {
    if (e.target.nodeName === "path" || e.target.nodeName === "SPAN") {
      this.setState({ tab: parseInt(e.target.parentNode.id, 10) });
    } else {
      this.setState({ tab: parseInt(e.target.id, 10) });
    }
  };

  render() {
    return <Main tab={this.state.tab} handleTabChange={this.handleTabChange} />;
  }
}

export default App;
