import { makeStyles } from "@material-ui/core/styles";

import { Grid, Hidden } from "@material-ui/core";

import SideNav from "../components/side-nav";
import SideDrawer from "../components/side-drawer";
import ImageAvatar from "../components/avatar";

import About from "./about";
import Education from "./education";
import Honors from "./honors";
import Certificates from "./certificates";
import Experience from "./experience";
import Skills from "./skills";
import Projects from "./projects";
import Contact from "./contact";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    containerdirection: "row",
    justify: "space-around",
    alignItems: "flex-start",
  },
  side: {
    containerdirection: "column",
    justify: "flex-start",
    alignItems: "flex-start",
  },
}));

function Main({ tab, handleTabChange }) {
  const classes = useStyles();
  const tabs = [
    <About />,
    <Education />,
    <Honors />,
    <Certificates />,
    <Experience />,
    <Skills />,
    <Projects />,
    <Contact />,
  ];

  return (
    <Grid container className={classes.root} spacing={2}>
      <Grid item xs={3}>
        <Hidden xsDown>
          <Grid className={classes.side}>
            <ImageAvatar />
            <SideNav tab={tab} handleTabChange={handleTabChange} />
          </Grid>
        </Hidden>
        <Hidden smUp>
          <Grid>
            <SideDrawer tab={tab} handleTabChange={handleTabChange} />
          </Grid>
        </Hidden>
      </Grid>

      <Grid item xs={7}>
        {tabs.map((tabItem, id) => {
          return (
            <div hidden={id !== tab} key={`tabpanel-${id}`}>
              {id === tab && <div>{tabItem}</div>}
            </div>
          );
        })}
      </Grid>
    </Grid>
  );
}

export default Main;
