import React from "react";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";

import { List, ListItem, ListItemIcon, ListItemText } from "@material-ui/core";
import Drawer from "@material-ui/core/Drawer";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";

import MenuIcon from "@material-ui/icons/Menu";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import FormatQuoteIcon from "@material-ui/icons/FormatQuote";
import SchoolIcon from "@material-ui/icons/School";
import EmojiEventsIcon from "@material-ui/icons/EmojiEvents";
import BookIcon from "@material-ui/icons/Book";
import WorkIcon from "@material-ui/icons/Work";
import BusinessCenterIcon from "@material-ui/icons/BusinessCenter";
import LaptopMacIcon from "@material-ui/icons/LaptopMac";
import MailIcon from "@material-ui/icons/Mail";

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: -3,
  },
  hide: {
    display: "none",
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: "nowrap",
  },
  drawerOpen: {
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerClose: {
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: "hidden",
    width: theme.spacing(7) + 1,
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(9) + 1,
    },
  },
  toolbar: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    padding: theme.spacing(0, 1),
    ...theme.mixins.toolbar,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
}));

export default function SideDrawer({ tab, handleTabChange }) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const myIcons = {
    "About ": (
      <FormatQuoteIcon id={0} color={tab === 0 ? "secondary" : "default"} />
    ),
    "Education ": (
      <SchoolIcon id={1} color={tab === 1 ? "secondary" : "default"} />
    ),
    "Honors and Awards": (
      <EmojiEventsIcon id={2} color={tab === 2 ? "secondary" : "default"} />
    ),
    "Certificates ": (
      <BookIcon id={3} color={tab === 3 ? "secondary" : "default"} />
    ),
    "Experience ": (
      <WorkIcon id={4} color={tab === 4 ? "secondary" : "default"} />
    ),
    "Skills ": (
      <BusinessCenterIcon id={5} color={tab === 5 ? "secondary" : "default"} />
    ),
    "Projects ": (
      <LaptopMacIcon id={6} color={tab === 6 ? "secondary" : "default"} />
    ),
    "Contact ": <MailIcon id={7} color={tab === 7 ? "secondary" : "default"} />,
  };

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  return (
    <div className={classes.root}>
      <Drawer
        variant="permanent"
        className={clsx(classes.drawer, {
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open,
        })}
        classes={{
          paper: clsx({
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open,
          }),
        }}
      >
        <div className={classes.toolbar}>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            className={clsx(classes.menuButton, {
              [classes.hide]: open,
            })}
          >
            <MenuIcon />
          </IconButton>
          <IconButton
            onClick={handleDrawerClose}
            className={clsx(classes.menuButton, {
              [classes.hide]: !open,
            })}
          >
            <ChevronLeftIcon />
          </IconButton>
        </div>
        <Divider />
        <List
          onClick={(event) => {
            handleTabChange(event);
            handleDrawerClose();
          }}
        >
          {Object.keys(myIcons).map((label, index) => (
            <ListItem id={index} button key={label}>
              <ListItemIcon id={index}>{myIcons[label]}</ListItemIcon>
              <ListItemText id={index} primary={label} />
            </ListItem>
          ))}
        </List>
      </Drawer>
    </div>
  );
}
