import React from "react";
import { makeStyles } from "@material-ui/core/styles";

import { Tab, Tabs } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  root: {
    color: "green",
    textShadow: "true",
    backgroundColor: "lightgrey",
    borderRadius: 5,
  },
  tabFull: {
    fullWidth: true,
  },
}));

export default function SideNav({ tab, handleTabChange }) {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Tabs
        centered
        orientation="vertical"
        variant="fullWidth"
        value={tab}
        onClick={(event) => handleTabChange(event)}
      >
        <Tab label="About" id={0} />
        <Tab label="Education" id={1} />
        <Tab label="Honors and Awards" id={2} />
        <Tab label="Certificates" id={3} />
        <Tab label="Experience" id={4} />
        <Tab label="Skills" id={5} />
        <Tab label="Projects" id={6} />
        <Tab label="Contact" id={7} />
      </Tabs>
    </div>
  );
}
